:root {
  /* dark shades of primary color*/
  --clr-primary-1: hsl(21, 91%, 17%);
  --clr-primary-2: hsl(21, 84%, 25%);
  --clr-primary-3: hsl(21, 81%, 29%);
  --clr-primary-4: hsl(21, 77%, 34%);
  --clr-primary-5: hsl(21, 62%, 45%);
  --clr-primary-6: hsl(21, 57%, 50%);
  --clr-primary-7: hsl(21, 65%, 59%);
  --clr-primary-8: hsl(21, 80%, 74%);
  --clr-primary-9: hsl(21, 94%, 87%);
  --clr-primary-10: hsl(21, 100%, 94%);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(210, 22%, 49%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(360, 71%, 66%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 620px;
}
.work-section {
  float: left;
  width: 100%;
  height: calc(100vh - 60px - 50px);
  display: flex;
  justify-content: center;
  align-items: center;
  /* align-items: center;
  justify-content: center; */
  /* background-color: red; */
  flex-direction: column;
  /* max-width: var(--max-width); */
}

@media screen and (min-width: 992px) {
  .section {
    width: 95vw;
  }
}
.work-section .title {
  text-align: center;
  /* margin-bottom: 2rem; */
}
.work-section .title h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
}
.work-section .title span {
  font-size: 0.85em;
  color: var(--clr-primary-5);
  margin-right: 1rem;
  font-weight: 700;
}
.work-section .section-center {
  margin: 0 auto;

  margin-top: 6rem;
  width: 30vw;
  /* have to have a height */
  height: 100%;
  max-width: 800px;
  text-align: center;
  position: relative;
  display: flex;

  /* overflow: hidden; */
}
.work-section .person-img {
  /* border-radius: 50%; */
  margin-bottom: 1rem;
  width: 80%;

  object-fit: cover;
  border: 4px solid var(--clr-grey-8);
  box-shadow: var(--dark-shadow);
}
.work-section article h4 {
  text-transform: uppercase;
  color: var(--clr-primary-5);
  /* margin-bottom: 0.25rem; */
}
.work-section .title {
  text-transform: capitalize;

  /* margin-bottom: 0.75rem; */
  color: var(--clr-grey-3);
}
.work-section .text {
  max-width: 35em;
  margin: 0 auto;
  width: 80%;
  /* margin-top: 2rem; */
  line-height: 2;
  color: var(--clr-grey-5);
}
.work-section .icon {
  font-size: 3rem;
  margin-top: 1rem;
  color: var(--clr-primary-5);
}
.work-section .prev,
.next {
  position: absolute;
  top: 180px;
  /* left: 6%; */
  transform: translateY(-50%);
  background: var(--clr-grey-5);
  color: var(--clr-white);

  display: flex;
  place-items: center;
  border-color: transparent;
  font-size: 1rem;
  border-radius: var(--radius);
  cursor: pointer;
  transition: var(--transition);
}
.work-section .prev:hover,
.next:hover {
  background: var(--clr-primary-5);
}
.work-section .prev {
  left: 0;
}
.work-section .next {
  right: 0;
}
@media (min-width: 800px) {
  .work-section .text {
    max-width: 45em;
  }
  .work-section .prev,
  .next {
    width: 2rem;
    height: 2rem;
    font-size: 1.5rem;
  }
}
.work-section article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: var(--transition);
}
.work-section article.activeSlide {
  opacity: 1;
  height: 500px;
  /* background-color: red; */
  transform: translateX(0);
}
.work-section article.activeSlide img {
  /* height: 300px; */
  object-fit: contain;
}
.work-section article.lastSlide {
  transform: translateX(-100%);
  opacity: 1;
  margin-top: 30px;
}
.work-section article.lastSlide img {
  height: 200px;
  object-fit: contain;
}
.work-section article.nextSlide {
  transform: translateX(100%);
  opacity: 1;
  margin-top: 30px;
}
.work-section article.nextSlide img {
  height: 200px;
  object-fit: contain;
}

@media screen and (max-width: 1000px) {
  .work-section .section-center {
    overflow: hidden;
    width: 80vw;
  }
}
